#news {
	width: 100%;
	height: 100vh;
	background: lightgreen;
	position: relative;
	padding: 10vw;
	overflow: hidden;
	h1 {
		font: bold 200px/1 'arial';
		color: #fff;
	}
	h2 {
		font: bold 150px/1 'arial';
		color: rgba(0, 0, 0, 0.3);
		position: absolute;
		top: 13vh;
		left: 100px;
	}
}
